    populateData() {
        debugger;
        const getdata = `
        query {
            FeeCategories  {
            FEECATEGORY_ID,
            FEECATEGORY_CODE,
            FEECATEGORY_NAME,
            FEECATEGORY_DESCRIPTION,
            RELATIVE_SIZE,
            SET_COA_ID,
            SET_CHART_OF_ACCOUNTS{
                COA_ID,
                COA_DESCRIPTION
                            },
            COACATEGORY_ID:    SET_COACATEGORY_ID

            COACategory{
                        COACATEGORY_ID
                        COACATEGORY_CODE
                                }
        COASUBCATEGORY_ID:     SET_COA_SUBCATEGORY_ID
            COASubCategory{
                        COASUBCATEGORY_ID
                        COASUBCATEGORY_CODE
                                }
                                SET_DEPARTMENT_ID
            SET_DEPARTMENTS{
                    DEPARTMENT_ID,
                    DEPARTMENT_NAME
                                    },
                SYS_STATUS_ID,
                SYSTEMSTATUS{
                    SYS_STATUS_ID,
                    SYS_STATUS_NAME
                },
                MODULE_ID: SET_MODULE_ID,
            Set_Modules{
                    MODULE_ID,
                    MODULE_NAME
                    },
            CREATEDBY,
            MODIFIEDBY,
            CREATED_DATE,
            MODIFIED_DATE
            }
        }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.FeeCategories
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

